import React, { useState } from "react";
import {
  MDBBtn,
  MDBModal,
  MDBModalDialog,
  MDBModalContent,
  MDBModalHeader,
  MDBModalTitle,
  MDBModalBody,
  MDBModalFooter,
  MDBFooter,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBIcon,
} from "mdb-react-ui-kit";
import { Link } from "react-router-dom";
import "./Footer.css";

export default function Footer() {
  const [scrollableModal, setScrollableModal] = useState(false);

  return (
    <MDBFooter className="text-center text-lg-start text-muted foooter">
      <section className="foooter foooter__main">
        <MDBContainer className="text-center text-md-start mt-5">
          <MDBRow className="mt-3">
            <MDBCol md="2" lg="2" xl="2" className="mx-auto mb-4">
              <h6 className="text-uppercase padding-left textwhite fw-bold mb-4">
                Menu
              </h6>
              <p className="textwhite">
                <Link to="/" className="text-reset">
                  Strona główna
                </Link>
              </p>
              <p className="textwhite">
                <Link to="/o-mnie" className="text-reset">
                  O mnie
                </Link>
              </p>
              <p className="textwhite">
                <Link to="/cennik" className="text-reset">
                  Cennik
                </Link>
              </p>
              <p className="textwhite">
                <Link to="/kontakt" className="text-reset">
                  Kontakt
                </Link>
              </p>
            </MDBCol>

            <MDBCol md="3" lg="2" xl="2" className="mx-auto mb-4">
              <h6 className="text-uppercase padding-left textwhite fw-bold mb-4">
                Przydatne linki
              </h6>
              <p
                onClick={() => setScrollableModal(!scrollableModal)}
                className="textwhite onclick"
              >
                Polityka prywatności
              </p>

              <MDBModal
                show={scrollableModal}
                setShow={setScrollableModal}
                tabIndex="-1"
              >
                <MDBModalDialog scrollable>
                  <MDBModalContent>
                    <MDBModalHeader>
                      <MDBModalTitle>POLITYKA PRYWATNOŚCI</MDBModalTitle>
                      <MDBBtn
                        className="btn-close"
                        color="none"
                        onClick={() => setScrollableModal(!scrollableModal)}
                      ></MDBBtn>
                    </MDBModalHeader>
                    <MDBModalBody>
                      <h6>TEKST W BUDOWIE</h6>
                      <p>
                       TEKST W BUDOWIE
                      </p>
                    </MDBModalBody>
                    <MDBModalFooter>
                      <MDBBtn
                        color="info"
                        onClick={() => setScrollableModal(!setScrollableModal)}
                      >
                        Zamknij
                      </MDBBtn>
                    </MDBModalFooter>
                  </MDBModalContent>
                </MDBModalDialog>
              </MDBModal>
            </MDBCol>

            <MDBCol md="4" lg="3" xl="3" className="mx-auto mb-md-0 mb-4">
              <h6 className="text-uppercase padding-left textwhite fw-bold mb-4">
                Kontakt
              </h6>
              <p>
                <MDBIcon icon="envelope" className="me-3" />
                <span className="textwhite">paulina.tokarczyk93@gmail.com</span>
              </p>
              <p>
                <MDBIcon icon="phone" className="me-3" />{" "}
                <span className="textwhite"> +48 660 053 872</span>
              </p>
            </MDBCol>
          </MDBRow>
        </MDBContainer>
      </section>

      <div
        className="text-center p-4 foooter__copyrights"
        style={{ backgroundColor: "rgba(0, 0, 0, 0.05)" }}
      >
        <p className="textwhite">
          © 2022 Copyright:{" "}
          <Link exact to="/" className="text-reset fw-bold">
            TERAPIA
          </Link>
        </p>
        <p className="textwhite">
          Strona stworzona przez:{" "}
          <a
            className="text-reset fw-bold"
            href="https://www.linkedin.com/in/stanek-michal/"
            target="_blank"
            rel="noreferrer"
          >
            STANEK MICHAŁ
          </a>
        </p>
      </div>
    </MDBFooter>
  );
}
